/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

.seccion {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .seccion {
    padding: 8rem 0;
  }
}

.btn-primario, .btn-plan {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.btn-primario:hover, .btn-plan:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .btn-primario, .btn-plan {
    width: auto;
    display: inline-block;
  }
}

.btn-plan {
  border-radius: 0.7rem;
  border: 2px solid #FFFFFF;
  margin-top: 2rem;
  display: block;
}

.header {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.1), rgb(0, 0, 0)), -webkit-image-set(url(../img/header.avif) 1x, url(../img/header.webp) 1x, url(../img/header.jpg) 1x);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  padding: 5rem 0;
}
.header__logo {
  max-width: 20rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__logo {
    margin: 0;
  }
}
.header__contenido {
  padding-top: 10rem;
  padding-bottom: 10rem;
}
@media (min-width: 1024px) {
  .header__contenido {
    padding-top: 15rem;
    padding-bottom: 15rem;
  }
}
.header__heading {
  color: #FFFFFF;
  font-size: 4.5rem;
  line-height: 1.5;
}
@media (min-width: 768px) {
  .header__heading {
    font-size: 6rem;
  }
}
@media (min-width: 1024px) {
  .header__heading {
    max-width: 40%;
  }
}

@media (min-width: 768px) {
  .crea__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.crea__heading {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .crea__heading {
    margin-top: 0;
  }
}
.crea__heading::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
}

.beneficios {
  background-color: #84d9bc;
}
@media (min-width: 768px) {
  .beneficios__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
}
.beneficios__contenido {
  grid-column: 1/4;
}
.beneficios__heading {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .beneficios__heading {
    margin-top: 0;
  }
}
.beneficios__heading::after {
  content: "";
  background-color: #FFFFFF;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
}
.beneficios__imagen {
  grid-column: 4/6;
  transition: transform 0.3s ease-in-out;
}
.beneficios__imagen:hover {
  transform: rotate(8deg) scale(1.1);
}

.estadisticas__heading {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .estadisticas__heading {
    margin-top: 0;
  }
}
.estadisticas__heading::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
}

.planes {
  background-color: #313c4e;
}
.planes__heading {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .planes__heading {
    margin-top: 0;
  }
}
.planes__heading::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
}
.planes__descripcion {
  color: #FFFFFF;
}

.nuestros-planes {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .nuestros-planes {
    flex-direction: row;
    max-width: 80rem;
    margin: 10rem auto 0 auto;
  }
}

.plan {
  flex-grow: 1;
  background-color: #FFFFFF;
  padding: 16rem 4rem 4rem 4rem;
  border-bottom: 1px solid #e1e1e1;
}
@media (min-width: 1024px) {
  .plan {
    border: none;
    transition: transform 0.3s ease-in-out;
  }
  .plan:hover {
    transform: scale(1.1);
  }
}
.plan.inicio {
  background-image: url("../img/icono_inicio.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
.plan.negocio {
  background-color: #84d9bc;
  order: -1;
  background-image: url("../img/icono_negocio.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
@media (min-width: 1024px) {
  .plan.negocio {
    order: unset;
    margin: -5rem 0;
    border-radius: 2rem;
    background-position: 50% 7rem;
    padding-top: 21rem;
    z-index: 1;
  }
}
.plan.pro {
  background-image: url("../img/icono_pro.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
.plan__precio {
  font-size: 1.8rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}

.listado__item {
  text-align: center;
  margin-bottom: 2rem;
}
.listado__item:last-of-type {
  margin-bottom: 0;
}

.testimoniales__imagen img {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .testimoniales {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
  .testimoniales__imagen {
    grid-column: 1/3;
  }
  .testimoniales__contenido {
    grid-column: 3/6;
  }
}
.testimoniales__heading {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .testimoniales__heading {
    margin-top: 0;
  }
}
.testimoniales__heading::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
}

.testimonial {
  background-color: #84d9bc;
  border: 5px solid #313c4e;
  margin-bottom: 4rem;
  padding: 2rem;
  border-radius: 1rem;
  position: relative;
}
@media (min-width: 1024px) {
  .testimonial {
    max-width: 70%;
  }
  .testimonial:nth-child(2) {
    margin-left: 10%;
  }
  .testimonial:nth-child(3) {
    margin-left: 20%;
  }
  .testimonial:nth-child(4) {
    margin-left: 30%;
  }
}
.testimonial::after {
  content: "";
  display: block;
  width: 5rem;
  height: 5rem;
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  background-position: center center;
  position: absolute;
  top: 2rem;
  left: 2rem;
}
.testimonial:last-of-type {
  margin-bottom: 0;
}
.testimonial__descripcion {
  margin: 0;
  padding-left: 6rem;
}

.footer {
  background-color: #313c4e;
  color: #FFFFFF;
}
.footer__grid {
  padding: 5rem 0;
  display: grid;
  gap: 4rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__logo {
  max-width: 20rem;
  margin: 0 auto 5rem auto;
}
@media (min-width: 768px) {
  .footer__logo {
    margin: 0;
  }
}
.footer__copyright {
  text-align: center;
  margin: 0 auto;
  color: #FFFFFF;
  padding: 2rem;
  border-top: 2px solid #84d9bc;
  max-width: 120rem;
  font-size: 1.2rem;
  text-transform: uppercase;
}

.footer-widget {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .footer-widget {
    margin: 0;
  }
}
.footer-widget__heading {
  color: #84d9bc;
  text-align: left;
}
@media (min-width: 768px) {
  .footer-widget__heading {
    margin-bottom: 3rem;
  }
}
.footer-widget__nav {
  display: flex;
  flex-direction: column;
}
.footer-widget__enlace {
  color: #FFFFFF;
  margin-bottom: 1.5rem;
}
.footer-widget__enlace:last-of-type {
  margin-bottom: 0;
}